import PropTypes from 'prop-types'
import React from 'react';
import "./select.scss";
import cx from "classnames";

function Select({variant, className, options, label, id, ...props}) {
  return (
    <div
      className={cx(`select ${className ? className : ''}`, {
        [`select--${variant}`]: variant,
      })}>
      <select id={id} name={id} {...props}>
        {label && <option value="" disabled selected>{label}</option>}
        {options.map(option => <option key={option} value={option}>{option}</option>)}
      </select>
      <div className="select__fake-arrow"/>
    </div>
  )
}


Select.propTypes = {
  variant: PropTypes.string,
  label: PropTypes.string,
  id: PropTypes.string,
  options: PropTypes.array,
  className: PropTypes.string,
};

export default Select
