import PropTypes from 'prop-types'
import React from 'react';
import "./divider.scss";

function Divider({ children, variant }) {
  return (
    <div className={`divider ${variant ? `divider--${variant}` : '' }`}>
      {children}
    </div>
  )
}

Divider.propTypes = {
  children: PropTypes.node,
  variant: PropTypes.string,
}

export default Divider
