import React from "react";
import PropTypes from 'prop-types'
import { Link } from '@reach/router'
import "./footer.scss";

function Footer({ children }) {
  return (
    <div className="footer">
      {children}
    </div>
  )
}

Footer.propTypes = {
  children: PropTypes.node.isRequired,
}

function FooterInfo({ children }) {
  return (
    <div className="footer__info">
      {children}
    </div>
  )
}

FooterInfo.propTypes = {
  children: PropTypes.node.isRequired,
}

function FooterMenu({ children, variant }) {
  return (
    <div className={`footer__menu ${variant ? `footer__menu--${variant}` : '' }`}>
      {children}
    </div>
  )
}

FooterMenu.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
}

function FooterMenuItem({ children, variant, to }) {
  return (
   <Link
     to={to}
     className={`footer__menu-item ${variant ? `footer__menu-item--${variant}` : '' }`}>
     {children}
   </Link>
  )
}

FooterMenuItem.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
  to: PropTypes.string.isRequired,
}

export { Footer, FooterInfo, FooterMenu, FooterMenuItem }
