import React from 'react'
import PropTypes from 'prop-types'
import {
  Footer,
  FooterInfo,
  FooterMenu,
  FooterMenuItem,
} from '../../components/Footer'
import { FaLinkedin, FaTwitterSquare, FaFacebook } from 'react-icons/fa'
import Container from '../../components/Container/Container'

function FooterBlock({ info, menuItems, socialLinks }) {
  return (
    <Container type="full" className="footer-block">
      <Footer>
        <FooterInfo>{info}</FooterInfo>

        <FooterMenu className="footer__menu">
          {menuItems &&
            menuItems.map((item) => (
              <FooterMenuItem key={item.text} to={item.url}>
                {item.text}
              </FooterMenuItem>
            ))}
        </FooterMenu>

        <FooterMenu variant="social" className="footer__social">
          {socialLinks.linkedin && (
            <FooterMenuItem to={socialLinks.linkedin} variant="social">
              <FaLinkedin className="footer__social-icon footer__social-icon--linkedin" />
            </FooterMenuItem>
          )}
          {socialLinks.twitter && (
            <FooterMenuItem to={socialLinks.twitter} variant="social">
              <FaTwitterSquare className="footer__social-icon footer__social-icon--twitter" />
            </FooterMenuItem>
          )}
          {socialLinks.facebook && (
            <FooterMenuItem to={socialLinks.facebook} variant="social">
              <FaFacebook className="footer__social-icon footer__social-icon--facebook" />
            </FooterMenuItem>
          )}
        </FooterMenu>
      </Footer>
    </Container>
  )
}

FooterBlock.propTypes = {
  info: PropTypes.string,
  menuItems: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      url: PropTypes.string,
    })
  ),
  socialLinks: PropTypes.shape({
    linkedin: PropTypes.string,
    twitter: PropTypes.string,
    facebook: PropTypes.string,
  }),
}

export default FooterBlock
