import PropTypes from 'prop-types'
import React from 'react';
import "./input.scss";
import cx from "classnames";

function Input({ label, variant, type = 'text', className, id, ...props }) {
  return (
    <div
      className={cx(`input ${className ? className : ''}`, {
        [`input--${type}`]: type,
        [`input--${variant}`]: variant,
      })}>
      <input id={id} name={id} type={type} {...props} />
      {type === 'file' && <label htmlFor={id} className="input__file-button">Choose File</label>}
      <span className="highlight" />
      <span className="bar" />
      <label>{label}</label>
    </div>
  )
}

Input.defaultProps = {
  type: 'text',
};

Input.propTypes = {
  label: PropTypes.string,
  variant: PropTypes.string,
  type: PropTypes.string,
  id: PropTypes.string,
  className: PropTypes.string,
};

export default Input
