import React from 'react'
import Input from '../Input'
import Cta from '../Cta'
import './apply-form.scss';
import Select from "../Select";
import { getNames } from 'country-list';

function ApplyForm() {
  return (
    <div className="apply-form">
      <Input label="Full Name" variant="fullwidth" required/>
      <Input label="Email" variant="fullwidth" required/>

      <Select label="English level" options={['Basic','Advanced']} required/>
      <Select label="Country of residence" options={getNames()} required/>

      <Input label="Attach your resume" id="resume" type="file" variant="fullwidth" required/>
      <Input label="Include cover letter" id="letter" type="file" variant="fullwidth" required/>

      <Cta to="#">Apply</Cta>
    </div>
  )
}

ApplyForm.propTypes = {};

export default ApplyForm
